'use client'

import { useState, useEffect } from 'react'
import { X } from 'lucide-react'
import { cn } from '@/lib/utils'
import { getWhatsAppUrl } from '@/lib/utils'

interface WhatsAppWidgetProps {
  phoneNumber?: string
  message?: string
}

export function WhatsAppWidget({
  phoneNumber = process.env.NEXT_PUBLIC_WHATSAPP_NUMBER || '507XXXXXXXX',
  message = 'Hola, me gustaría obtener información sobre sus servicios.',
}: WhatsAppWidgetProps) {
  const [isVisible, setIsVisible] = useState(false)
  const [showTooltip, setShowTooltip] = useState(false)

  useEffect(() => {
    // Show widget after a short delay
    const timer = setTimeout(() => setIsVisible(true), 1000)
    
    // Show tooltip after 3 seconds
    const tooltipTimer = setTimeout(() => setShowTooltip(true), 3000)
    
    return () => {
      clearTimeout(timer)
      clearTimeout(tooltipTimer)
    }
  }, [])

  const handleClick = () => {
    setShowTooltip(false)
    window.open(getWhatsAppUrl(phoneNumber, message), '_blank', 'noopener,noreferrer')
  }

  const dismissTooltip = (e: React.MouseEvent) => {
    e.stopPropagation()
    setShowTooltip(false)
  }

  return (
    <div
      className={cn(
        'fixed bottom-6 right-6 z-50 flex items-end gap-3',
        'transition-all duration-500',
        isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-8'
      )}
    >
      {/* Tooltip */}
      {showTooltip && (
        <div
          className={cn(
            'relative bg-white rounded-lg shadow-elevated p-4 max-w-[240px]',
            'animate-slide-up'
          )}
        >
          <button
            onClick={dismissTooltip}
            className="absolute -top-2 -right-2 p-1 bg-white rounded-full shadow-md hover:bg-beige transition-colors"
            aria-label="Dismiss"
          >
            <X className="h-3 w-3 text-warm-gray" />
          </button>
          <p className="text-sm text-dark">
            ¿Tienes preguntas? <br />
            <span className="font-medium">¡Escríbenos por WhatsApp!</span>
          </p>
          {/* Arrow */}
          <div className="absolute right-4 -bottom-2 w-4 h-4 bg-white rotate-45 shadow-md" />
        </div>
      )}

      {/* WhatsApp Button */}
      <button
        onClick={handleClick}
        className={cn(
          'relative p-4 rounded-full shadow-lg',
          'bg-[#25D366] hover:bg-[#128C7E]',
          'text-white transition-all duration-300',
          'hover:scale-110 active:scale-95',
          'focus:outline-none focus:ring-4 focus:ring-[#25D366]/30'
        )}
        aria-label="Contact via WhatsApp"
      >
        {/* Pulse Animation */}
        <span className="absolute inset-0 rounded-full bg-[#25D366] animate-ping opacity-30" />
        
        {/* Icon */}
        <svg
          viewBox="0 0 24 24"
          fill="currentColor"
          className="h-7 w-7 relative z-10"
        >
          <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.374a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893a11.821 11.821 0 00-3.48-8.413Z" />
        </svg>
      </button>
    </div>
  )
}
